import { useParams } from "react-router-dom";
import ProjectGallery from "../components/projects/ProjectGallery";
import ProjectHeader from "../components/projects/ProjectHeader";
import ProjectInfo from "../components/projects/ProjectInfo";
import ProjectRelatedProjects from "../components/projects/ProjectRelatedProjects";
import { SingleProjectProvider } from "../context/SingleProjectContext";
import { motion } from "framer-motion";
import { useEffect, useState } from "react";
import ApiLink from "../API";
import axios from "axios";
import { Container, Paper, Skeleton } from "@mui/material";
import tableJoditStyle from "../css/tableJoditStyle.module.css";
import styled from "@emotion/styled";
const ContainerBox = styled(Container)(({ theme }) => ({
  // width: "100%",
  img: {
    maxWidth: "100%",
  },
  marginTop: 1,
  // table: {
  //   border: "solid #000 !important",
  //   borderWidth: "1px 0 0 1px !important",
  // },
  // td: {
  //   border: "solid #000 !important",
  //   borderWidth: "0 1px 1px 0 !important",
  // },
  // th: {
  //   border: "solid #000 !important",
  //   borderWidth: "0 1px 1px 0 !important",
  // },
  // // tr: { border: "1px solid" },
  // td: { border: "1px solid" },
}));
const ProjectSingle = () => {
  let { docId } = useParams();
  const [openBackdrop, setopenBackdrop] = useState(false);
  const [ViewData, setViewData] = useState({});
  async function LoadProject(docId) {
    try {
      setopenBackdrop(true);
      const response = await axios.get(
        ApiLink + "/api/documenthtml/getbyid/" + docId,
        {
          withCredentials: true,
          headers: {
            token: localStorage.getItem("token"),
          },
        }
      );
      if (response.status === 200) {
        setViewData(response.data.data[0]);
        setopenBackdrop(false);
      } else if (response.status === 401) {
        //got to login and clear token
      }
    } catch (error) {}
  }
  useEffect(() => {
    LoadProject(docId);
  }, []);
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, delay: 1 }}
      transition={{
        ease: "easeInOut",
        duration: 0.6,
        delay: 0.15,
      }}
      className="container mx-auto mt-5 sm:mt-10"
    >
      <SingleProjectProvider>
        <ProjectHeader ViewData={ViewData} />
        <Paper
          variant="outlined"
          square
          sx={{
            pt: 1,
            mt: 1,
            borderRight: "none",
            // width:"100%"
            // overflow: "auto",
            // display: "flex",
            // minWidth: "80vw",
          }}
        >
          <ContainerBox>
            {openBackdrop && (
              <Skeleton
                variant="rectangular"
                animation="wave"
                width="70vw"
                height="80vh"
              />
            )}
            <div
              className={tableJoditStyle}
              dangerouslySetInnerHTML={{
                __html: ViewData?.docHTML,
              }}
            />
          </ContainerBox>
        </Paper>
        {/* <ProjectGallery /> */}
        {/* <ProjectInfo /> */}
        {/* <ProjectRelatedProjects /> */}
      </SingleProjectProvider>
    </motion.div>
  );
};

export default ProjectSingle;
